.portrait-wrapper {
  display: contents;
}

// Non-clickable portraits (div instead of a)
div.portrait {
  cursor: default;
  pointer-events: none;
}

.portrait {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 20px;
  width: 175px;
  max-width: calc(100% - 20px - 20px);
  text-decoration: none;
}

.portrait[data-style="small"] {
  width: 100px;
}

.portrait[data-style="tiny"] {
  flex-direction: row;
  gap: 15px;
  width: unset;
  text-align: left;
}

.portrait .icon {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(20px + 10%);
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  background: var(--background);
  box-shadow: var(--shadow);
  transform: translate(14%, 14%);
}

.portrait[data-style="small"] .icon {
  left: -2px;
  top: -2px;
}

.portrait[data-style="tiny"] .icon {
  display: none;
}

.portrait-image {
  width: 100%;
  margin-bottom: 20px;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  object-fit: cover;
  object-position: center center;
  box-shadow: var(--shadow);
}

// Specific positioning for Vasundhara's image
.portrait-image[src*="vasundhara-krishnan"] {
  object-position: center 30%;
}

// Specific positioning for Florian's image
.portrait-image[src*="florian-wehmhoener"] {
  object-position: 25% 20%;
}

.portrait[data-style="tiny"] .portrait-image {
  width: 50px;
  margin: 0;
}

.portrait-name {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
}

.portrait[data-style="tiny"] {
  .portrait-description,
  .portrait-affiliation {
    display: none;
  }
}
